@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&family=Roboto:wght@400;700&display=swap');


.projects-container {
  padding: 35px;
  width: 80%;
  font-family: 'Roboto', sans-serif;
  margin-bottom: 20px;
}

.projects-title {
  font-family: 'Montserrat', sans-serif;
  font-size: 33px;
  font-weight: bold;
  margin-top: 65px;
  line-height: 1.2;
  margin-bottom: 35px;
  text-align: center;
  color: #1C1678;
}

.projects-title1 {
  font-family: 'Arial';
  font-size: 15px;
  line-height: 1.2;
  font-weight: bold;
  margin-bottom: 35px;
  text-align: center;
  color: #1C1678;
}

.project {
  margin-top: 15px;
  background-color: #fff;
  padding: 20px;
  margin-bottom: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease-out, box-shadow 0.3s ease-out;
}

.project:hover {
  transform: translateY(-10px);
  box-shadow: 0 8px 16px #c415b833;
}

.project h2 {
  font-size: 24px;
  font-weight: bold;
  color: black;
  margin-bottom: 10px;
}

.project p {
  margin-bottom: 10px;
  line-height: 1.6;
  color: #333;
}

.button1, .button2 {
  display: inline-block;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: bold;
  color: #fff;
  background-color: #860b7e;
  border: white solid 5px;
  border-radius: 10px;
  text-align: center;
  text-decoration: none;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.button1:hover, .button2:hover {
  background-color: #333;
  transform: scale(1.05);
}

@media (max-width: 100%) {
  .projects-container {
      width: 95%;
      padding: 20px;
  }

  .project h2 {
      font-size: 20px;
  }
}

