.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 95vh;
  text-align: center;
  width: 100%;
  position: fixed;
  color: white;
  margin-top: 3rem;
  background: linear-gradient(135deg, #fbc2eb 0%, #a6c1ee 100%);
  padding: 0 1rem; /* Add some padding for small screens */
}

.title {
  font-size: 3rem;
  margin-bottom: 1rem;
}


.subtitle {
  font-size: 1.5rem;
  color: white;
  margin-bottom: 2rem;
  margin-right: 30px;
}

@media (max-width: 1024px) {
  .title {
      font-size: 2.5rem;
  }

  .subtitle {
      font-size: 1.3rem;
  }
}

@media (max-width: 768px) {
  .title {
      font-size: 2rem;
  }

  .subtitle {
      font-size: 1.2rem;
  }
}

@media (max-width: 100%) {
  .container {
      height: auto;
      padding: 2rem 1rem; 
      margin-top: 0; 
  }

  .title {
      font-size: 1.8rem;
  }

  .subtitle {
      font-size: 1rem;
  }
}
