@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&family=Roboto:wght@400;700&display=swap');

body, html {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  color: #fff;
  height: 100%; 
  
}

.parent-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh; /* Ensure full viewport height */
  background: linear-gradient(135deg, #a1c4fd 0%, #2E3A87 100%);
  padding: 20px;
  width: 100%; /* Ensure full viewport width */
  max-width: 1200px; /* Limit maximum width for content */
  margin: auto; /* Center align content */
  color: #fff;
  font-family: 'Roboto', sans-serif;
}

.styled-motion-div {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 20px;
  padding: 20px;
  width: 100%; /* Span full width of parent */
  max-width: 800px; /* Limit maximum width for content */
  text-align: center;
  background: linear-gradient(135deg, #a1c4fd 0%, #2E3A87 100%);
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  font-family: 'Montserrat', sans-serif;
}

.content {
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 100%; /* Span full width of parent */
  max-width: 800px; /* Limit maximum width for content */
  text-align: left;
  margin: 20px 0;
  color: #333;
}

.subtitle1 {
  font-size: 18px;
  line-height: 1.6;
  margin-bottom: 10px;
  font-weight: bold;
  color: black;
}

@media (max-width: 768px) {
  .styled-motion-div, .content {
    width: 95%; /* Adjust width for smaller screens */
    max-width: 95%; /* Limit maximum width for smaller screens */
  }

  .subtitle1 {
    font-size: 16px;
  }
}
